//gender and tattoos onoff switch-----------------------------------------

.onoff, .tattoos {
  // display: -moz-inline-stack;
  // display: inline-block;
  // *vertical-align: auto;
  zoom: 1;
  position: relative;
  cursor: pointer;
  width: 100%;
  height: 40px;
  line-height: 37px;
  border: 1px solid #ee2562;
  margin-bottom: 19px;
}

.onoff label,
.tattoos label {
  position: absolute;
  top: -8px;
  width: 100%;
  height: 100%;
  cursor: pointer;
  background: #fff;
  font-weight: bold;
  color: #FFF;
  -webkit-transition: background 0.3s, text-indent 0.3s;
  -moz-transition: background 0.3s, text-indent 0.3s;
  -o-transition: background 0.3s, text-indent 0.3s;
  transition: background 0.3s, text-indent 0.3s;
  text-indent: 70px;
}

.onoff label:after {
  content: 'Female';
  display: block;
  position: absolute;
  top: 0px;
  left: 140px;
  width: 100%;
  font-size: 12px;
  color: #ee2562;
  z-index: 1;
}

.tattoos label:after{
  content: 'HELL YAS';
  display: block;
  position: absolute;
  top: 0px;
  left: 140px;
  width: 100%;
  font-size: 12px;
  color: #ee2562;
  z-index: 1;	
}

.onoff label:before,
.tattoos label:before {
  content: '';
  width: 155px;
  height: 39.4px;
  border-radius: 2px;
  background: #ee2562;;
  position: absolute;
  z-index: 2;
  top: 0px;
  left: 0px;
  display: block;
  -webkit-transition: left 0.3s;
  -moz-transition: left 0.3s;
  -o-transition: left 0.3s;
  transition: left 0.3s;
}

.onoff input:checked + label,
.tattoos input:checked + label {
  background: #fff;
  text-indent: -80px;
}

.onoff input:checked + label:after{
  content: 'Male';
  color: #ee2562;
}

.tattoos input:checked + label:after{
  content: 'HELL NO';
  color: #ee2562;
}

.onoff input:checked + label:before,
.tattoos input:checked + label:before {
  left: 160px;
  background-color: #ee2562;
}


// age range slider --------------------------------------------------------
#rangevalue{
	color: #ee2562;
	font-size: 13px;
}

#slidecontainer {
    width: 100%;
}

.slider {
    -webkit-appearance: none;
    width: 100%;
    height: 25px;
    background: #fff;
    outline: none;
}

.slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 27px;
    height: 30px;
    background: #ee2562;
    cursor: pointer;
}

.slider::-moz-range-thumb {
    width: 27px;
    height: 30px;
    background: #ee2562;
    cursor: pointer;
}


